@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;600&family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
    align-items: center;
    background-image: url('https://www.canyon-news.com/wp-content/uploads/2016/09/Sunset-Boulevard.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    height: 150px;
    justify-content: center;
}

.title-container {
    background-color: rgba(255, 255, 255, .8);
    border-radius: 25px;
    box-shadow: 0px 0px 40px 20px rgb(255, 255, 255);
    padding: 5px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

h1 {
    color: rgb(255, 0, 162);
    font-family: 'Dancing Script', cursive;
    font-size: 40px;
    font-weight: bold;
}

h2 {
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: lighter;
}

@media screen and (min-width: 480px) {
    header {
        height: 200px;
    }

    h1 {
        font-size: 50px;
    }

    h2 {
        font-size: 20px;
    }
}

