.card {
    background-color: rgba(255, 255, 255, 1);
    height: 180px;
    margin-bottom: 20px;
    padding: 5px;
    width: 150px;
}

.img-container {
    overflow-x: hidden;
    width: 100%;
}

img {
    width: 100%;
}

h3 {
    color: rgb(0, 162, 146);
    font-family: 'Dancing Script', cursive;
    font-size: 20px;
    margin: 5px 0;
}

.text {
    font-size: 11px;
    line-height: 1.5;
    text-decoration: none;
}

a:link {
    color: rgb(0, 0, 0);
}

a:hover {
    color: rgb(255, 0, 162);
}

a:active {
    color: rgb(255, 0, 162);
}

a:visited {
    color: rgb(0, 0, 0);
}

@media screen and (min-width: 480px) {
    .card {
        height: 230px;
        padding: 7px;
        width: 200px;
    }

    h3 {
        font-size: 24px;
    }

    .text {
        font-size: 14px;
    }

    #insta-icon {
        font-size: 16px;
    }
}

@media screen and (min-width: 642px) {
    .card {
        height: 250px;
        margin-bottom: 40px;
        padding: 10px;
        width: 250px;
    }

    
}

@media screen and (min-width: 885px) {
    .card {
        margin-left: 25px;
        margin-right: 25px;
    }
}

@media screen and (min-width: 1740px) {
    .card {
        margin: 50px;
    }
}
* {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  text-align: center;
}

html {
  min-height: 100vh;
  overflow-y: scroll;
}

body {
  min-height: 100vh;
}

main {
  background-image: url('https://i.pinimg.com/originals/4c/99/41/4c99419d837bd388f5418cd620c283d2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  min-height: calc(100vh - 150px);
}

.options {
  background-color: rgba(255, 255, 255, .8);
  display: flex;
  justify-content: space-evenly;
  padding: 10px;
}

span {
  font-size: 12px;
  padding-right: 2px;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  min-height: 100%;
  padding-top: 20px;
}

@media screen and (min-width: 480px) {
  span {
    font-size: 14px;
  }
}

@media screen and (min-width: 642px) {
  .options {
    margin-bottom: 20px;
    padding: 10px;
  }
}

@media screen and (min-width: 850px) {
  .options {
    padding: 10px 10%;
  }
}

@media screen and (min-width: 1340px) {
  .flex-container {
    padding-left: 100px;
    padding-right: 100px;
  }
}

@media screen and (min-width: 1540px) {
  .flex-container {
    padding-left: 200px;
    padding-right: 200px;
  }
}

@media screen and (min-width: 1977px) {
  .flex-container {
    padding-left: 300px;
    padding-right: 300px;
  }
}


